<p><span class="rvts42">心灵手巧</span></p>
</td>
<td width="478" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">+2</span><span class="rvts21">主动性，</span><span class="rvts20">+5%</span><span class="rvts21">暴击几率</span></p>
</td>
</tr>
<tr valign="top">
<td width="326" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber35.jpg"></p>
</td>
<td width="180" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
