</td>
<td width="167" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">节俭</span></p>
</td>
<td width="478" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">+1</span><span class="rvts21">讨价还价</span></p>
</td>
</tr>
