<p><span class="rvts39"></span>
<br><span class="rvts41">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="100" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
