</td>
<td width="215" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="101" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="735" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="100" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber36.jpg"></p>
</td>
<td width="215" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Encourage</span></p>
