<p class="rvps2"><span class="rvts39"></span>
<br><span class="rvts41">使用技巧：</span>
<br><span class="rvts21">人类种族拥有较高的主动性和额外的暴击几率，因此是一个比较具备爆发力的种族，并且人类可以在起初得到额外的商店价格优惠。鼓舞技能被誉为所有种族中的</span><span class="rvts20"></span><span class="rvts21">最好技能</span><span class="rvts20"></span><span class="rvts21">，瞬间提升基础属性（额外增加了生命值），相当于群疗</span><span class="rvts20">+</span><span class="rvts21">群</span><span class="rvts20">BUFF</span><span class="rvts21">提升。</span></p>
